@media screen and (max-width: 991px) {
}

@media screen and (max-width: 425px) {
  .menu-icon {
    display: block;
    position: absolute;
    top: 0;
    right: 0;
    transform: translate(-100%, 60%);
    font-size: 1.8rem;
    cursor: pointer;
  }

  .nav-menu {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    width: 100vw;
    height: 100vh;
    position: absolute;
    top: 0px;
    left: -100%;
    opacity: 0;
    transition: all 0.7s ease;
    background-color: #fafafa;
    z-index: 999;
  }

  .nav-menu.active {
    top: 50px;
    left: 0;
    opacity: 1;
    transition: all 0.7s ease;
  }

  .nav-item {
    margin-bottom: 20px;
  }

  .nav-links {
    font-size: 20px;
  }
}

