.dropdown-menu {
  background: red;
  width: 200px;
  position: absolute;
  top: 80px;
  list-style: none;
  text-align: start;
}

.dropdown-menu li {
  background: #1888ff;
  cursor: pointer;
}

.dropdown-menu li:hover {
  background: #5cabff;
}

.dropdown-menu.clicked {
  display: none;
}

.dropdown-link {
  display: block;
  height: 100%;
  width: 100%;
  text-decoration: none;
  color: #fff;
  padding: 16px;
}

@media screen and (max-width: 960px) {
  .fa-caret-down {
    display: none;
  }
}

.qna__wrapper {
  display: flex;
  justify-content: center;
  align-items: center;
  flex-flow: column;
}

.title h2 {
  font-weight: bold;
  font-family: Merriweather, sans-serif;
  font-size: 1rem;
  margin-bottom: 0.5rem;
  font-style: italic;
  opacity: 0.7;
}

.project__show {
  display: flex;
  width: 100%;
  object-fit: contain;
}

.project__show div {
  width: 200px;
  height: 200px;
}
.project__show img {
  object-fit: contain;
  max-width: 100%;
  height: auto;
}

